.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #E59866;
}

.heading {
  margin: 3rem auto;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  background-color: #f1f1f1;
}

p {
  font-size: 2rem;
  line-height: 2;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.seccion {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .seccion {
    padding: 5rem 0;
  }
}

.header {
  background-image: linear-gradient(to right, rgba(229, 152, 102, 0.3) 0%, rgba(229, 152, 102, 0.3) 100%), url(../img/header.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .header {
    padding-bottom: 10rem;
  }
}
.header__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 1rem;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 15rem;
  filter: invert(1);
}
.header__heading {
  color: #FDFEFE;
  text-align: center;
  font-size: 4rem;
  margin: 3rem 0;
}
.header__texto {
  color: #FDFEFE;
  text-align: center;
  margin: 0 0 3rem 0;
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    gap: 1rem;
  }
}
.navegacion__link {
  color: #FFFFFF;
  margin-bottom: 1.5rem;
  padding: 1rem 2rem;
  font-weight: 700;
  text-transform: uppercase;
  transition: background-color 0.3s;
}
.navegacion__link:last-of-type {
  margin-bottom: 0;
}
.navegacion__link:hover {
  background-color: #E59866;
}
@media (min-width: 768px) {
  .navegacion__link {
    margin-bottom: 0;
  }
}
.navegacion__link--activo {
  border-bottom: 2px solid #FFFFFF;
}

.contenido-principal {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .contenido-principal {
    grid-template-columns: 3fr 2fr;
    column-gap: 3rem;
  }
}

.blogs__heading {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #000;
  text-align: center;
}
@media (min-width: 768px) {
  .blogs__heading {
    padding: 5rem 0 0 0;
  }
}
@media (min-width: 768px) {
  .blogs__heading {
    margin-top: 0;
  }
}
.blogs__heading::after {
  content: "";
  background-color: #E59866;
  margin: -1.4rem 0 0;
  width: calc(100% + 1rem);
  height: 1rem;
  display: block;
}

.blog {
  margin-bottom: 3rem;
  border-bottom: 1px solid #e1e1e1;
}
.blog:last-of-type {
  border-bottom: 0;
}
.blog__nombre {
  padding: 2rem 0;
  margin: 0;
}
.blog__texto {
  margin: 0 0 2rem 0;
  font-size: 1.6rem;
}
.blog__enlace {
  background-color: #E59866;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  text-transform: uppercase;
  margin-bottom: 3rem;
}
.blog__enlace:hover {
  cursor: pointer;
  background-color: #de7b3a;
}
@media (min-width: 768px) {
  .blog__enlace {
    width: 50%;
  }
}

.side {
  margin-bottom: 3rem;
}
.side__titulo {
  text-align: left;
  text-transform: none;
  margin: 0 0 1rem 0;
}
.side__descripcion {
  line-height: 1.5;
  font-size: 1.6rem;
}
.side__precio, .side__cupos {
  margin: 0;
  font-size: 2rem;
}
.side__cupos--span, .side__precio--span {
  font-weight: 700;
}
.side__enlace {
  margin-top: 2rem;
  background-color: #E59866;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  text-transform: uppercase;
}
.side__enlace:hover {
  cursor: pointer;
  background-color: #de7b3a;
}
@media (min-width: 768px) {
  .side__enlace {
    width: 50%;
  }
}

.nosotros__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .nosotros__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.nosotros__texto {
  font-size: 2rem;
  line-height: 1.5;
  margin: 0;
}

.curso {
  border-bottom: 1px solid #e1e1e1;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .curso {
    display: grid;
    grid-template-columns: 40% 60%;
    column-gap: 3rem;
  }
}
.curso:last-of-type {
  border-bottom: 0;
  margin-bottom: 0;
}
.curso__titulo {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #000;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .curso__titulo {
    margin-top: 0;
  }
}
.curso__titulo::after {
  content: "";
  background-color: #E59866;
  margin: -1.4rem 0 0;
  width: calc(100% + 1rem);
  height: 1rem;
  display: block;
}
.curso__cupos, .curso__precio {
  margin: 0;
}
.curso__cupos--span, .curso__precio--span {
  font-weight: 700;
}
.curso__descripcion {
  font-size: 1.8rem;
}

.contacto__imagen {
  background-image: url("../img/contacto.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  height: 50rem;
}
.contacto__formulario {
  background-color: #FFFFFF;
  width: 95%;
  margin: -5rem auto 0 auto;
  border-radius: 1rem;
  padding: 5rem 2rem 5rem 2rem;
}
@media (min-width: 768px) {
  .contacto__formulario {
    padding: 5rem;
  }
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo__label {
  flex: 0 0 9rem;
  text-align: right;
  padding-right: 2rem;
}
.campo__input {
  flex: 1;
  border: 1px solid #e1e1e1;
  padding: 1rem;
}
.campo__input--textarea {
  height: 20rem;
}
.campo__submit {
  background-color: #E59866;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.campo__submit:hover {
  cursor: pointer;
  background-color: #de7b3a;
}
@media (min-width: 768px) {
  .campo__submit {
    width: 20rem;
    margin: 0 0 0 auto;
  }
}

.footer {
  background-color: #E59866;
  padding: 1rem 2rem 0 2rem;
  margin-top: 3rem;
}
.footer__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer__logo {
  width: 10rem;
}
.footer__link:last-of-type {
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .footer__link:last-of-type {
    margin-bottom: 0;
  }
  .footer__link:hover {
    background-color: #de7b3a;
  }
}
.footer__copy {
  margin: 0;
  padding: 0 0 1rem 0;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
}